import React, { Component } from 'react';
import { BrowserRouter, Routes, Route, Link } from "react-router-dom";

class ToggledMenu extends Component {
    constructor(props) {
        super(props);
        this.state = {
            cartImg: this.props.cartlogo,
        }
    }

    componentDidMount() {
        console.log (this.props.cartlogo);
    }

    render() {
        return (
            <ul className="nav-toggled-menu">
                <li><Link to='/'>Home</Link></li>
                <li><Link to="/products">Products</Link></li>
                <li><Link to="/shopping_cart"><img className="cart-logo" src={this.state.cartImg} alt='cart'></img></Link></li>
            </ul> 
        )
    }
}

export default ToggledMenu;