import React, { Component, useState } from 'react';
import { useNavigate } from "react-router-dom";
import OrderList from './OrderList';
import TotalPrice from './TotalPrice';

function ShoppingCart(props) {
    const [isEmpty, setIsEmpty] = useState(props.isempty);
    let navigate = useNavigate();

    return (
        <div className='main sp'>
            {isEmpty ?
            <div>
                <OrderList orderlist={props.orderlist}/>
                <TotalPrice orderlist={props.orderlist}/>
            </div>                 
        : <div className="shopping-list-empty">
            <h1> You don't have anything in your shopping list.</h1>
            <p> Go start shopping!</p>
            <div>
                <button onClick={()=>{ return navigate('/products') }}>Click here!</button>
            </div>
          </div>}
        </div>
    )
}

export default ShoppingCart;