import logo from './logo.svg';
import './App.css';
import { BrowserRouter, Routes, Route, Link, useNavigate} from "react-router-dom";
import Navbar from './components/Navbar';
import Home from './components/Home';
import Products from './components/Products';
import ShoppingCart from './components/ShoppingCart';
import React, { useEffect, useState } from 'react';
import ToggledMenu from './components/ToggledMenu';

function App() {
  const [orders, setOrders] = useState([]);
  const [isEmpty, setIsEmptpy] = useState(false);
  const [toggleMenu, setToggleMenu] = useState(false);
  const [cartLogo, setCartLogo] = useState("https://cdn-icons.flaticon.com/png/512/665/premium/665865.png?token=exp=1661022082~hmac=69efd04a4bb52d385f181e02788d94ac")

  useEffect(() => {
  }, [orders, isEmpty, toggleMenu])

  const updateOrders = (img, des, amount, price, id, name) => {
    setIsEmptpy(true);
    setOrders([...orders, {'img': img, 'description': des, 'amount': amount, 'price': price, 'id': id, 'name': name}]);
  }

  const togglesMenu = () => {
    if (!toggleMenu) {
      setToggleMenu(true);
    }
    else {
      setToggleMenu(false); 
    }
  }

  return (
    <div className="app">
      <BrowserRouter>
        <Navbar cartlogo={cartLogo} togglesmenu={togglesMenu}/>
        {(toggleMenu) ? <ToggledMenu cartlogo={cartLogo} /> : null}
        <Routes>
          <Route path='/shopping-cart-TOP/' element={<Home />}></Route>
          <Route path='/shopping-cart-TOP/products' element={<Products updateorders={updateOrders}/>}></Route>
          <Route path='/shopping-cart-TOP/shopping-cart' element={<ShoppingCart orderlist={orders} isempty={isEmpty}/>}></Route>
        </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;

