import React, { Component } from 'react';
import uniqid from 'uniqid';

class OrderList extends Component {
    constructor(props) {
        super(props);
        this.state = {
        }
    }

    componentDidMount() {
        console.log (this.props.orderlist);
    }

    render() {
        return (
            <div className='order-list'>
                {this.props.orderlist.map((order) => {
                    return ( 
                    <div key={uniqid()} className="order-detail">
                        <div className="order-img-container">
                           <img className="order-detail-picture" src={order.img} alt="img"></img>
                        </div>
                        <div className='order-detail-texts'>
                            <p>{order.name}</p>
                            <p>{order.description}</p>
                            <p>{order.price}</p>
                            <p>x {order.amount}</p>
                        </div>
                    </div> )
                })}
            </div>
        )
    }
}

export default OrderList;