import React, { Component } from 'react';

class Product extends Component {
    constructor(props) {
        super(props);
        this.state = {
            addIcon: "https://cdn-icons.flaticon.com/png/512/2997/premium/2997933.png?token=exp=1660936742~hmac=52d7a9c09da1cf3c61c1478195608d5c",
            subsIcon: "https://cdn-icons-png.flaticon.com/512/1828/1828901.png",
            count: 1,
            price: this.props.price,
            imgSrc: this.props.src,
            description: this.props.des,
            name: this.props.name,
        }
        this.addsProduct = this.addsProduct.bind(this);
        this.subsProduct = this.subsProduct.bind(this);
    }

    addsProduct() {
        this.setState({
            count: this.state.count+1,
        })
    }

    subsProduct() {
        if (this.state.count <= 1) {
            return;
        }
        else {
            this.setState({
                count: this.state.count-1,
            })   
        } 
    }

    render() {
        return (
            <div className='product'>
                <div className='product-container'>
                    <div className="product-img-container">
                        <img className="product-img" src={this.state.imgSrc} alt="product"></img>
                    </div>
                    <div className="product-detail">
                        <div><p className="product-text">{this.state.name}</p></div>
                        <div><p className="product-text">{this.state.description}</p></div>
                        <div><p className="product-text">{this.state.price}</p></div>
                        <div className='product-pay'>
                            <div className="product-order-detail">
                                <img className='products-icon' src={this.state.addIcon} alt="add" onClick={this.addsProduct}></img>
                                <p> {this.state.count} </p>
                                <img className='products-icon' src={this.state.subsIcon} 
                                alt="substract" onClick={this.subsProduct}></img>
                            </div>
                        </div>
                        <div className='product-btn-group'>
                                <button className="product-cart-button" type="button" onClick={()=>{ return this.props.updateorders(this.props.src, this.state.description, this.state.count, this.state.price, this.props.id, this.props.name) }}>Buy</button>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Product;