import React, { Component } from 'react';
import { BrowserRouter, Routes, Route, Link } from "react-router-dom";

class Navbar extends Component {
    constructor(props) {
        super(props)
        this.state = {
            logoImg: "https://img.freepik.com/free-vector/merry-christmas-background-with-christmas-ball-silhouette_1361-3477.jpg?w=740&t=st=1661071185~exp=1661071785~hmac=82af5bb1150f0e7217e9fcd63de9d9836ed8047a2717f1e56e18a26996dc23f5",
            screenWidth: window.innerWidth,
            menuImg: "https://cdn-icons.flaticon.com/png/512/2099/premium/2099125.png?token=exp=1661023954~hmac=66a07e431a6b0ab670a5094b4902b994",
            cartImg: this.props.cartlogo,
        }
    }

    componentDidMount() {
        console.log ("initial screen width: "+this.state.screenWidth);
        window.addEventListener('resize', e => {
            this.setState({
                screenWidth: window.innerWidth,
            })
        })
    }
    
    componentDidUpdate() {
        console.log(this.state.screenWidth);
    }

    render() {
        return (
            <div>
                    {(this.state.screenWidth > 790) ?
                        <div className="navbar">
                            <div className="logo">
                                <Link to='/shopping-cart-TOP/'><img src={this.state.logoImg} alt="logo" className="logo-img"></img></Link>
                                <h2><Link to='/shopping-cart-TOP/'>CHRISTMAS SHOP</Link></h2>
                            </div>
                            <ul className="nav-menu">
                                <li><Link to='/shopping-cart-TOP/'>HOME</Link></li>
                                <li><Link to="/shopping-cart-TOP/products">PRODUCTS</Link></li>
                                <li><Link to="/shopping-cart-TOP/shopping-cart"><img className="cart-logo" src={this.state.cartImg} alt='cart'></img></Link></li>
                            </ul> 
                        </div>           
                    :                         
                    <div className="navbar">
                        <div className="logo">
                            <img src={this.state.logoImg} alt="logo" className="logo-img"></img>
                            <h1> Christmas Shop </h1>
                        </div>
                        <ul className="nav-menu">
                            <li><img src={this.state.menuImg} alt="menu" className="menu-btn" onClick={this.props.togglesmenu}></img></li>
                        </ul> 
                    </div>     
                }
            </div>
        )
    }
}

export default Navbar;