import React, { Component } from 'react';

class TotalPrice extends Component {
    constructor(props) {
        super(props);
        this.state = {
            totalPrice: 0,
        }
    }

    componentDidMount() {
        this.calculateTotal();
    }

    calculateTotal() {
        let total = 0;
        for (let i = 0; i < this.props.orderlist.length; i++) {
            const curPrice = this.props.orderlist[i].price*this.props.orderlist[i].amount;
            total += curPrice;
        }

        this.setState({
            totalPrice: total,
        })
    }

    render() {
        return (
            <div className="pay-container">
                <h2> Total Price </h2>
                <h2> {this.state.totalPrice} </h2>
                <button type="button"> Pay </button>
            </div>
        )
    }
}

export default TotalPrice;